/* autoprefixer grid: autoplace */

$bg-color: whitesmoke;
$shdow-gray: #d3d3d5;
$orange: #da4d1d;
$blue: #2b26c3;
$img-url: "../img/autonomous.png";
$img-url2: "../img/felicity.png";
$img-url3: "../img/romana.png";
$img-url4: "../img/wiki.png";
$img-url5: "../img/iss-locator.png";
$padding: 2rem;
$padding-long: 3.5rem;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  overflow-x: hidden;
}
.App .tiltedEffect {
  height: 190px;
  position: absolute;
  transform: skewY(-12deg);
  transform-origin: 0;
}

.App .tiltedEffect:nth-child(4) {
  // Select the 4th span inside .block
  width: 33.33333%;
  bottom: 30%;
  left: -10%;
  z-index: 1;

  background: rgba(31, 162, 255, 0.3);
}

.App .tiltedEffect:nth-child(1) {
  // Select the 4th span inside .block
  width: 100%;
  top: 13%;
  right: 10%;
  // background: rgba(129, 198, 246, 0.2);
  background: linear-gradient(
    150deg,
    rgba(85, 51, 255, 0.15) 15%,
    #05d5ff44 70%,
    #a6ffcb3d 94%
  );
}
.App .tiltedEffect:nth-child(2) {
  // Select the 4th span inside .block
  width: 12.33333%;
  bottom: 30%;
  right: 0%;
  transform: skewY(-35deg);
  transform-origin: 0;
  // background: rgba(129, 198, 246, 0.4);
  background: linear-gradient(
    150deg,
    rgba(7, 209, 255, 0.5) 15%,
    rgba(75, 231, 232, 0.5) 70%,
    #a6ffcb 94%
  );
}
.container .tiltedEffect_container {
  height: 190px;
  position: absolute;
  transform: skewY(-12deg);
  transform-origin: 0;
}

.container .tiltedEffect_container:nth-child(4) {
  // Select the 4th span inside .block
  width: 33.33333%;
  bottom: -40%;
  left: -10%;
  z-index: -11;

  background: linear-gradient(
    150deg,
    rgba(85, 51, 255, 0.027) 15%,
    #05d5ff3b 70%,
    #a6ffcb10 94%
  );
}

.container .tiltedEffect_container:nth-child(1) {
  // Select the 4th span inside .block
  width: 33.33333%;
  bottom: -70%;
  right: 0%;
  z-index: -11;

  background: linear-gradient(
    150deg,
    rgba(7, 210, 255, 0.568) 15%,
    rgba(75, 232, 232, 0.24) 50%,
    #a6ffcb48 94%
  );
}
.container .tiltedEffect_container:nth-child(2) {
  // Select the 4th span inside .block
  width: 100%;
  bottom: -190%;
  right: 0%;
  transform: skewY(-35deg);
  transform-origin: 0;
  // background: rgba(129, 198, 246, 0.4);
  background: linear-gradient(
    150deg,
    rgba(252, 254, 255, 0.157) 15%,
    rgba(202, 236, 236, 0.15) 70%,
    #a6ffcb 94%
  );
}

.container .tiltedEffect_container:nth-child(3) {
  // Select the 4th span inside .block
  width: 100%;
  bottom: -250%;
  right: 0%;
  transform: skewY(-35deg);
  transform-origin: 0;
  // background: rgba(129, 198, 246, 0.4);
  background: linear-gradient(
    150deg,
    rgba(166, 255, 203, 1) 15%,
    rgba(202, 236, 236, 0.15) 70%,
    rgba(181, 207, 192, 0.198) 94%
  );
}

.intro {
  z-index: 0;
  background: linear-gradient(150deg, #53f 15%, #05d5ff 70%, #a6ffcb 94%);
  height: 70vh;
  display: grid;
  grid-template-columns: [page-h-start] 1fr [title-start] 5fr [avatar-start]1fr[avatar-end title-end] 1fr [page-h-end];
  grid-template-rows: [page-v-start]1fr [title-v-start]10fr [title-v-end subtitle-start] 5fr[subtitle-end social-start]1fr [social-end] 0.5fr[page-v-end];
}

.intro_Avatar {
  grid-column: avatar-start/avatar-end;
  grid-row: title-v-start/title-v-end;
}

.intro_Circular {
  position: relative;
  top: 26px;
  left: 43px;
  grid-column: avatar-start/avatar-end;
  grid-row: title-v-start/title-v-end;
}

.intro__Title {
  height: 100%;
  display: flex;
  font-family: "IBM Plex Mono", sans-serif;
  font-size: 4.5rem;
  z-index: 3;
  font-weight: 400;
  color: whitesmoke;
  grid-column: title-start/title-end;
  grid-row: title-v-start/title-v-end;
}

.intro__Subtitle {
  height: 100%;
  display: flex;
  font-family: "Roboto", sans-serif;
  grid-column: title-start/title-end;
  grid-row: subtitle-start/subtitle-end;
  z-index: 3;
  font-weight: 400;
  font-size: 1.25rem;
  color: #000036;
  line-height: 1.75rem;
}

.shortIntro {
  display: none;
}

.intro__Social {
  height: 100%;
  display: flex;
  grid-column: title-start/title-end;
  grid-row: social-start/social-end;
  justify-content: space-between;
}

.intro__Social i {
  opacity: 1;
  font-size: 2rem;
  color: rgb(0, 0, 0);
}

h2 {
  pointer-events: none;
}

body {
  background-color: $bg-color;
  width: 100vw;
  height: 100vh;
}

.container i {
  opacity: 0;
  font-size: 36px;
  color: #fff;
  will-change: transform;
  -webkit-transform: scale(0.1);
  transform: scale(0.1);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.container {
  z-index: 3;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .card {
    z-index: 1;
    position: relative;
    width: 375px;
    height: 200px;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 4px;

    &:before {
      content: "";
      z-index: 99;
      position: absolute;
      top: -10px;
      left: 32px;
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 16px;
      background-color: $bg-color;
    }
    &:after {
      content: "";
      z-index: 99;
      position: absolute;
      bottom: -10px;
      left: 32px;
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 16px;
      background-color: $bg-color;
    }

    // h2 {
    //   z-index: 199;
    //   font-family: "Arial", sans-serif;
    //   position: absolute;
    //   bottom: 0;
    //   right: 130px;
    //   font-size: 2rem;
    //   font-weight: 700;
    //   color: #fff;
    // }
    .fa-arrow-right {
      z-index: 100;
      position: absolute;
      right: 15px;
      bottom: 5px;
      font-size: 40px;
      cursor: pointer;
    }
    p {
      z-index: 99;
      position: absolute;
      top: 20px;
      right: 50px;
      color: rgb(31, 31, 31);
      opacity: 0.7;
      font-size: 1rem;
      letter-spacing: 1px;
      writing-mode: vertical-rl;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }
    .pic {
      z-index: 101;
      width: 280px;
      height: 200px;
      background-image: url($img-url);
      background-size: 100% 100%;
      filter: grayscale(100%);
    }

    .social {
      position: absolute;
      left: 60px;
      top: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
      justify-content: space-around;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      width: 180px;
      height: 64px;
      border-radius: 80px;
      z-index: 300;
    }

    &:hover i {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }

    button {
      z-index: 23;
      position: absolute;
      right: 14px;
      bottom: 14px;
      width: 30px;
      height: 30px;
      background-color: $orange;
      border: none;
      border-radius: 30px;
      cursor: pointer;
      outline: none;
      transition: all 0.3s ease;
      mix-blend-mode: hard-light;

      i {
        font-size: 3rem;
      }
    }

    &:hover button {
      transform: scale(16.5);

      -ms-transform: scale(16.5);
      @supports (-webkit-appearance: none) {
        border-radius: 0px;
        -webkit-transform: scale(4.4, 20);
      }
    }

    &:hover p {
      color: #fff;
    }

    &:hover .pic {
      filter: grayscale(0);
    }
  }

  .card2 {
    .pic {
      background-image: url($img-url2);
    }
    button {
      background-color: $blue;
    }
  }
  .card3 {
    .pic {
      background-image: url($img-url3);
    }
    button {
      mix-blend-mode: hard-light;
      background-color: #630000;
    }
  }
  .card4 {
    .pic {
      background-image: url($img-url4);
    }
    button {
      background-color: #093824;
    }
  }
  .card5 {
    .pic {
      background-image: url($img-url5);
    }
    button {
      mix-blend-mode: darken;
      background-color: #c6ccb2;
    }
    &:hover p {
      color: #4b3b47;
    }
  }
}

.dr {
  position: absolute;
  bottom: 16px;
  right: 16px;
  width: 100px;
}

