@media screen and (max-width: 380px) and (min-width: 300px) {
  .intro_Circular {
    position: relative;
    top: 5px;
    left: -4px;
  }
  .intro_Avatar {
    position: relative;
    top: 10px;
    grid-column: avatar-start/avatar-end;
    grid-row: title-v-start/title-v-end;
